import React, { FC, ReactElement } from 'react';
import {
  StepApi,
  StepBody,
  StepDescription,
  StepLabels,
  StepEndpoint,
  StepNumber,
  StepTitle,
  StyledGettingStartedLabel,
  StyledGettingStartedStepDescription,
  StyledGettingStartedStepImage,
  StyledGettingStartedStep,
} from './styled';
import { Text } from '../common/Text';
import { PrimaryButton } from '../common/Buttons/Buttons';
import { Link } from 'gatsby';
import { InternalLinks } from '../../utils/links';

interface GettingStartedStepProps {
  stepNumber: number;
  stepTitle: string;
  stepDescription: string;
  stepApiName: string;
  stepApiEndpoint: string;
  stepApiColor: string;
  image: ReactElement;
}

interface GettingStartedLabelProps {
  labelText: string;
}

export const GettingStartedStep: FC<GettingStartedStepProps> = (props) => {
  const {
    stepDescription,
    stepNumber,
    stepTitle,
    stepApiColor,
    stepApiEndpoint,
    stepApiName,
    image,
  } = props;

  return (
    <StyledGettingStartedStep>
      <StyledGettingStartedStepDescription>
        <StepNumber>
          <span>{stepNumber}</span>
        </StepNumber>
        <StepBody>
          <StepTitle>{stepTitle}</StepTitle>
          <StepDescription>{stepDescription}</StepDescription>
          <StepLabels>
            <StepApi backgroundColor={stepApiColor}>
              <Text.Regular.Normal.sm>{stepApiName}</Text.Regular.Normal.sm>
            </StepApi>
            <StepEndpoint>
              <Text.Regular.Normal.sm>{stepApiEndpoint}</Text.Regular.Normal.sm>
            </StepEndpoint>
          </StepLabels>
        </StepBody>
      </StyledGettingStartedStepDescription>
      <StyledGettingStartedStepImage>{image}</StyledGettingStartedStepImage>
    </StyledGettingStartedStep>
  );
};

export const GettingStartedLabel: FC<GettingStartedLabelProps> = ({ labelText }) => {
  return <StyledGettingStartedLabel>{labelText}</StyledGettingStartedLabel>;
};

export const GetDemoButton = () => {
  return (
    <Link to={InternalLinks.GetDemo}>
      <PrimaryButton
      >
        Get a Demo
      </PrimaryButton>
    </Link>
  );
};
